@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100;200;300;400;500;600;700;800&display=swap);
.login{overflow:hidden;background-color:#292c33;padding:80px 40px 80px 40px;border-radius:10px;position:absolute;top:50%;left:50%;max-width:calc(100vh - 40px);-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);transition:box-shadow 300ms,-webkit-transform 300ms;transition:transform 300ms,box-shadow 300ms;transition:transform 300ms,box-shadow 300ms,-webkit-transform 300ms}.login::before,.login::after{content:"";position:absolute;width:400px;height:600px;border-top-left-radius:40%;border-top-right-radius:40%;border-bottom-left-radius:35%;border-bottom-right-radius:40%;z-index:-1}.login::before{left:50%;bottom:-50%;background-color:#434854;opacity:.7;-webkit-animation:wawes 25s infinite linear;animation:wawes 25s infinite linear}.login::after{left:30%;bottom:-100%;background-color:#434854;opacity:.5;-webkit-animation:wawes 25s infinite;animation:wawes 25s infinite}@-webkit-keyframes wawes{from{-webkit-transform:rotate(0)}to{-webkit-transform:rotate(360deg)}}@keyframes wawes{from{-webkit-transform:rotate(0);transform:rotate(0)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.login>img{border:2px solid #000;border-radius:20px}.login>img:hover{border:2px solid #a9b5d4;border-radius:20px;cursor:pointer}
h1{color:#fff;text-align:center;font-size:50px}a{color:#fff;display:block;text-align:center}div{position:absolute;width:500px;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);margin:0}
body{font-family:"JetBrains Mono",monospace;background-color:#000}
